<template>
  <div id="shanHe">
    <div id="spotHead">华夏山河</div>
    <spot-list :spotResult="spotResult"></spot-list>
  </div>
</template>

<script>
import spotList from "./spotList.vue";
import about from "../../../views/About.vue";
var AV = about.AV;
export default {
  name: "shanHe",
  data() {
    return {
      spotResult: ""
    };
  },
  components: {
    spotList
  },
  mounted() {
    var query = new AV.Query("spots");
    var that = this;
    query.equalTo("country", "中国");
    query.find().then(function(data) {
      that.spotResult = JSON.parse(JSON.stringify(data));
      console.log(that.spotResult);
    });
  }
};
</script>

<style scoped="scoped">
#spotHead {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: start;
  margin: 2% 0 1% 14%;
  width: 70%;
  background-color: paleturquoise;
  color: white;
  letter-spacing: 0;
  text-shadow: 0px 1px 0px #999, 0px 2px 0px #888, 0px 3px 0px #777,
    0px 4px 0px #666, 0px 5px 0px #555, 0px 6px 0px #444, 0px 7px 0px #333,
    0px 8px 7px #001135;
  font-size: 30px;
  padding: 0.5% 0 1% 2%;
}
</style>
